@import "../../_vars";

#Background {
  background-image: url("../../images/mebg.png");
  @include __bgImgVec;
  background-attachment: local;
  background-size: cover;
  background-position: left bottom;
  .background {
    @include __bgDimmer;
    align-items: flex-start;
    padding-top: 0;
    padding-bottom: 0;
    background-color: rgba($color: #000000, $alpha: 0.01);
    .container {
      height: 100%;
      overflow: hidden;
      .MuiTab-root{
        outline: none;
      }
      .MuiTab-textColorPrimary{
        color: #2b2b2b;
      }
      .tab-panel{
        width: 100%;
      }
      .card {
        background-color: rgba($color: #ededed, $alpha: 0.90);
        backdrop-filter: blur(2px);
        border-radius: 7px;
        min-height: 600px;
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: center;
        padding: 64px 32px 64px 32px;
      }
      .logo {
        display: inline-block;
        width: 32px;
        height: 32px;
        border-radius: 100%;
        border: transparent 2px double;
        margin-right: 7px;
      }
      .more {
        font-family: "Nunito", sans-serif;
        font-size: 18px;
        width: 100%;
      }
      .date,
      .location {
        margin-bottom: 16px;
        svg {
          color: #8b8b8b;
        }
        span {
          margin-left: 10px;
          color: #6b6b6b;
          font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
            monospace;
        }
      }
      .description {
        font-size: 17px;
      }
      .degree,
      .gpa,
      .relevants,
      .role,
      .description {
        margin-top: 7px;
      }
    }
  }
  @media only screen and (max-width: $xl) {
    background-attachment: fixed;
    height: fit-content;
    .background{
      .container{
        width: 100%;
        height: fit-content !important;
        min-height: 100vh;
        padding: 0;
        overflow: visible !important;
        .tab-title{
          display: none;
        }
        .MuiBox-root{
          padding: 0;
        }
        .card{
          padding: 10px 50px;
          .description, .relevants {
            font-size: 15px;
          }
        }
      }
    }
  }
}
