@import '../../_vars';

#Home{
  background: url('../../images/background.jpg');
  @include __bgImg;
  height: 100vh;
  .background{
    @include __bgDimmer;
    background-color: rgba(11, 11, 11, 0.356);
  }
  .particles{
    position: absolute;
    background-color: transparent;
  }
  .details{
    position: absolute;
    top: 15%;
    right: 25%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    .prof-avatar{
      border-radius: 100%;
      border: 17px ridge #000;
      position: relative;
      bottom: 0px;
      width: auto;
      height: 250px;
    }
  }
  .lastupdate{
    position: absolute;
    bottom: 5px;
    margin-left: 15px;
  }
  @media only screen and (max-width: $xl) {
    background-position: left bottom;
    background-size: auto 125vh;
    .details{
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 100%;
      padding: 25px;
      text-align: center;
      .prof-avatar{
        // display: none;
        width: 200px;
        height: 200px;
      }
    }
  }
}